import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from "@angular/router";
import { ClientService, ShopService } from "../service";
import { Store } from "@ngrx/store";
import { ApplicationState } from "../reducers";
import { getClientAccount } from "../actions";
import { Injectable } from "@angular/core";

@Injectable()
export class clientResolver implements Resolve<any>{
    constructor(private store: Store<any>,private clientService:ClientService, private shopService:ShopService){}


    resolve(route: ActivatedRouteSnapshot,state: RouterStateSnapshot){
        return new Promise((resolve,reject) => {
            this.store.select('app').subscribe((data:ApplicationState)=>{
            if(!data.client){
                this.store.dispatch(new getClientAccount());
                    resolve();
                }
            })
        });
        
    }
}