import { Injectable } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

@Injectable()
export class AppRouteService {

  constructor(
    private route: ActivatedRoute,
    private router: Router,
  ) {}

  businessDataArray: Array<any>;

  nextRoute(url:string,data:any){
    switch(url){
      case 'business-personal':
      return this.saveBusinessData(data);
    }
  }

  saveBusinessData(data:any):Array<any>{
    
    this.businessDataArray.push(data);
    return this.businessDataArray;
  }
}
