import { Injectable } from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, tap, exhaustMap} from 'rxjs/operators';
import {AuthService, ShopService, ClientService} from '../service';
import {of} from 'rxjs/observable/of';
import {Router} from '@angular/router';
import { Store } from '@ngrx/store';
import { loginUserTransaction , loginUserSuccess , loginUserFailed , loginProfileTransaction , loginProfileFailed , loginProfileSuccess, getProfile } from '../actions';
import { LOGIN_USER, LOGIN_PROFILE, LOGIN_USER_SUCCESS, LOGIN_PROFILE_SUCCESS, loginInvalidCredentials } from '../actions/login.actions';
import { getClientAccount } from '../actions/client.actions';


@Injectable()
export class LoginEffects {


@Effect()
LoginUser$ = this.actions$.pipe(
  ofType(LOGIN_USER),
  mergeMap((action:loginUserTransaction) => this.clientService.login(action.payload).pipe(
      map((data:any) => {
        if(data.body.token && data.body.success){
          this.authService.setAuthToken(data.body.token);
          this.store.dispatch(new getClientAccount());
          return new loginUserSuccess(data.body.message);
        }else{
          return new loginInvalidCredentials(data.body.message);
        }
      }),
      catchError((data:any) => {return of(new loginUserFailed(data.body.message))})
    )
  ),
);

@Effect()
LoginProfile$ = this.actions$.pipe(
  ofType(LOGIN_PROFILE),
  mergeMap((action:loginProfileTransaction) => this.shopService.login(action.payload).pipe(
      map((data:any) => {
        if(data.body.token){
          this.authService.setAuthToken(data.body.token);
          this.store.dispatch(new getProfile());
          return new loginProfileSuccess(data.body.message);
        }else{
          return new loginInvalidCredentials(data.body.message);
        }
      }),
      catchError((data:any) => {return of(new loginProfileFailed(data.body.message))})
    )
  ),
);

@Effect({dispatch:false})
loginSuccess$ = this.actions$.pipe(
  ofType(LOGIN_USER_SUCCESS),
  mergeMap(() => this.router.navigate(['user-profile']))
);

/* @Effect({dispatch:false})
loginProfileSuccess$ = this.actions$.pipe(
  ofType(LOGIN_PROFILE_SUCCESS),
  mergeMap(() => this.router.navigate(['profile']))
); */




constructor(private store:Store<any>, private actions$: Actions, private shopService: ShopService,private router: Router, private authService: AuthService, private clientService:ClientService ) {}

}

