import { addBusinesAddressInfo } from '../../actions/business.actions';
import { Component, OnInit,ViewChild,Input } from '@angular/core';
import {GooglePlaceDirective} from 'ngx-google-places-autocomplete';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { Store } from '@ngrx/store';
import { Address } from '../../models/barber.model';

@Component({
  selector: 'app-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.css']
})
export class AddressComponent implements OnInit {

  @Input('address') public AddressForm:FormGroup;
  businessAddress:Address;
  client$:Observable<any>;
  options:Object;
  componentForm = {
    street_number: 'short_name',
    route: 'long_name',
    locality: 'long_name',
    administrative_area_level_1: 'short_name',
    country: 'long_name',
    postal_code: 'short_name'
  };
  @ViewChild("placesRef")
  public gMapElementRef: GooglePlaceDirective;
  constructor(private store:Store<any>, private fb:FormBuilder) { }

  ngOnInit() {
    this.options = {
      types:['address'],
      componentRestrictions: { country: 'CA' }
    };
  }



  public handleAddressChange(address: any) {
    for (let component in this.componentForm) {
      (<HTMLInputElement>document.getElementById(component)).value = '';
    }

    for(let i = 0; i < address.address_components.length; i++){
      const addressType = address.address_components[i].types[0];
      if(this.componentForm[addressType]){
        (<HTMLInputElement>document.getElementById(addressType)).value = address.address_components[i][this.componentForm[addressType]];
        this.AddressForm.controls[addressType].setValue(address.address_components[i][this.componentForm[addressType]]);

      }
    }

  }

}
