import { Action } from '@ngrx/store';

export const PAY_APPOINTMENT = 'PAY_APPOINTMENT';
export const PAY_APPOINTMENT_SUCCESS = 'PAY_APPOINTMENT_SUCCESS';
export const PAY_APPOINTMENT_FAILED = 'PAY_APPOINTMENT_FAILED ';


export class payAppointment implements Action{
    readonly type = PAY_APPOINTMENT;
    constructor(public payload:any){}
}

export class payAppointmentSuccess implements Action{
    readonly type = PAY_APPOINTMENT_SUCCESS;
    constructor(public payload:any){}
}

export class payAppointmentFailed implements Action{
    readonly type = PAY_APPOINTMENT_FAILED;
    constructor(public payload:any){}
}

export type paymentActions = payAppointment | payAppointmentSuccess | payAppointmentFailed;