import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import {  Appointment, ClientBookingState } from '../../models/barber.model';
import { createAppointment } from '../../actions/booking.actions';
import { ApplicationState } from 'src/app/reducers';
import { SessionService } from 'src/app/service/session-service';


@Component({
  selector: 'app-booking-appointment',
  templateUrl: './booking-appointment.component.html',
  styleUrls: ['./booking-appointment.component.css']
})
export class BookingAppointmentComponent implements OnInit {
  bookingForm:FormGroup;
  startDate: Date;
  endDate: Date;
  hairStore:Array<Object>;
  twoWeeks: number;
  bookingDetails:Appointment;
  paymentSelection:any;
  paymentAmount:number;
  paidOnlyBookingFee:boolean;
  bookingFee:number = 1500;
  storeData:ClientBookingState;
  shopName:string;


  constructor(private store:Store<any>,private fb:FormBuilder, private session:SessionService) { }

  ngOnInit() {
    this.store.select('app').subscribe((data:ApplicationState)=>{    
      if(data.booking && data.booking.loaded){
        this.storeData = data.booking;
      }else{
        this.session.retrieveStoreBookedInfo();
      }
    });
    this.twoWeeks = 14;
    this.endDate = this.setValidDate();
    this.hairStore = [
      {'hairjob': 'Hair Cut', 'price':'3000'},
      {'hairjob': 'Line Up', 'price':'2500'},
      {'hairjob': 'Beard Clean Up', 'price':'2500'}
    ]

    this.createBookingForm();
  }

  createBookingForm(){
    this.bookingForm = this.fb.group({
      firstName: new FormControl('', Validators.required),
      lastName: new FormControl('', Validators.required),
      email: new FormControl('', [Validators.required, Validators.email]),
      appointmentDate: new FormControl('', Validators.required),
      job: new FormControl('', Validators.required),
      paymentSelection: new FormControl('', Validators.required),
    })
  }

  onChange(){
    if(this.bookingForm.controls.paymentSelection.value){
      this.paymentAmount = this.bookingFee;
    }else{
      this.paymentAmount = this.bookingFee + parseInt(this.bookingForm.controls.job.value.price);
    }

  }

  setValidDate(){
    let currentDay = new Date().getUTCDate();
    let month = new Date().getMonth();
    let year = new Date().getFullYear();
    let hour = new Date().getHours();
    let minutes = new Date().getMinutes();
    let twoWeekFromNow = currentDay + this.twoWeeks;
    this.startDate = new Date(year,month,currentDay,12,30);
    const setEndDate = new Date(year,month,twoWeekFromNow,23,30) ;
    return setEndDate;
  }

  filterSundays = (d: Date): boolean => {
    const day = d.getDay();
    return day !== 6;
  };

  BookingAppointment(){

    this.bookingDetails = {
      shop:this.storeData.bookingInfo.businessName,
      id:this.storeData.bookingInfo._id,
      firstName:this.bookingForm.controls.firstName.value,
      lastName:this.bookingForm.controls.lastName.value,
      email:this.bookingForm.controls.email.value,
      appointmentDay:this.bookingForm.controls.appointmentDate.value,
      serviceType: this.bookingForm.controls.job.value.hairjob,
      priceOfService:this.bookingForm.controls.job.value.price,
      balance:this.paymentAmount,
      fullServicePaid:this.bookingForm.controls.paymentSelection.value
    }

    this.store.dispatch(new createAppointment(this.bookingDetails))
  }
}
