import { OnInit, Injectable } from "@angular/core";
import { getProfileSuccess, getClientAccountSuccess, fetchAllBusinessessSuccess, fetchShop, viewProfileAppointments, updateBusinessAccountSuccess, UpdateClientSuccess, bookAppointmentSuccess } from "../actions";
import { Store } from "@ngrx/store";

@Injectable()
export class SessionService{

    shopID:string;
    UserSessionData:any;
    StoreBookedData:any;
    ShopSessionData:any;
    BusinessSessionData:any;
    constructor(private store:Store<any>){}

    
    retriveShopSession(){
        this.ShopSessionData = sessionStorage.getItem('BUSINESS_USER');
        if(this.ShopSessionData !== null){
            const profileData = JSON.parse(this.ShopSessionData);
                this.store.dispatch(new updateBusinessAccountSuccess(profileData));
              this.store.dispatch(new viewProfileAppointments());
        }
    }

    retriveUserSession(){
        this.UserSessionData = sessionStorage.getItem('USER');
        if(this.UserSessionData !== null){
            const userData = JSON.parse(this.UserSessionData);
            this.store.dispatch(new UpdateClientSuccess(userData));
        }
    }

    retrieveStoreBookedInfo(){
        this.StoreBookedData = sessionStorage.getItem('BOOKINGSTORE');
        if(this.StoreBookedData !== null){
            const bookingInfo = JSON.parse(this.StoreBookedData);
            this.store.dispatch(new bookAppointmentSuccess(bookingInfo));
        }
    }

    retriveAllShopsFromSession(){
        this.BusinessSessionData = sessionStorage.getItem('SHOPS');
        this.UserSessionData = sessionStorage.getItem('USER');
        if(this.BusinessSessionData !== null){
            const shopsData = JSON.parse(this.BusinessSessionData);
            const userData = JSON.parse(this.UserSessionData);
            this.store.dispatch(new UpdateClientSuccess(userData));
            this.store.dispatch(new fetchAllBusinessessSuccess(shopsData));
        }
    }

    retrieveStoreFromSession(){
        this.UserSessionData = sessionStorage.getItem('USER');
        this.BusinessSessionData = sessionStorage.getItem('SHOPS');
        this.shopID = sessionStorage.getItem('VIEWSHOPID');
        if(this.shopID !== null && this.BusinessSessionData !== null){
            const shopsDataArray:Array<any> = JSON.parse(this.BusinessSessionData);
            const id = JSON.parse(this.shopID);
            const userData = JSON.parse(this.UserSessionData);
            //const shop = shopsDataArray.filter((elem) => elem._id === id);
            this.store.dispatch(new UpdateClientSuccess(userData));
            this.store.dispatch(new fetchAllBusinessessSuccess(shopsDataArray));
            this.store.dispatch(new fetchShop(id));
        }

    }

    clearSessionData(){
        sessionStorage.clear();
    }

}