import { Component, OnInit, NgZone, OnDestroy, HostListener } from '@angular/core';
import { Store } from '@ngrx/store';
import { Router,
  // import as RouterEvent to avoid confusion with the DOM Event
  Event as RouterEvent,
  NavigationStart,
  NavigationEnd,
  NavigationCancel,
  NavigationError } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ApplicationState } from './reducers';
import { Subscription } from 'rxjs';
import { updateAppState } from './actions/application.actions';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {
  sub:Subscription;
  showHeader:boolean;
  loading:boolean;
  browserRefresh:boolean;
  constructor(private store:Store<any>,private router:Router, private spinner: NgxSpinnerService, private ngZone:NgZone){
     this.router.events.subscribe((event:RouterEvent) => {
      if (event instanceof NavigationStart) {
        this.browserRefresh = !router.navigated;
      }
    });
  }

  ngOnInit(){
    this.sub = this.store.select('app').subscribe((data:ApplicationState) => {
    
    });
  }

  @HostListener('window:beforeunload',['$event'])
  saveAppState($event){
    console.log($event);
  }
  
  

 // Shows and hides the loading spinner during RouterEvent changes
  navigationInterceptor(event: RouterEvent): void {
      if (event instanceof NavigationStart) {
        this.spinner.show();
      }
      if (event instanceof NavigationEnd || event instanceof NavigationCancel || event instanceof NavigationError) {
        this.spinner.hide();
      }
  }  

  ngOnDestroy(){
    this.sub.unsubscribe();
  }
  
}
