import { Action } from '@ngrx/store';

export const USER_PROFILE = 'USER_PROFILE';
export const UPDATE_USER_PROFILE = 'UPDATE_USER_PROFILE';
export const DELETE_USER_APPOINTMENT = 'DELETE_USER_APPOINTMENT';
export const VIEW_PROFILE_APPOINTMENTS = 'VIEW_PROFILE_APPOINTMENTS';
export const GET_PROFILE = 'GET_PROFILE';
export const GET_PROFILE_SUCCESS = 'GET_PROFILE_SUCCESS';
export const GET_PROFILE_FAILED = 'GET_PROFILE_FAILED';
export const LOGOUT_PROFILE = 'LOGOUT_PROFILE';
export const LOGOUT_PROFILE_SUCCESS = 'LOGOUT_PROFILE_SUCCESS';
export const LOGOUT_PROFILE_FAILED = 'LOGOUT_PROFILE_FAILED';
export const VIEW_PROFILE = 'VIEW_PROFILE';

export class getProfile implements Action{
  readonly type = GET_PROFILE;
  constructor(){}
}

export class viewProfile implements Action{
  readonly type = VIEW_PROFILE;
  constructor(){}
}

export class updateUserProfile implements Action{
  readonly type = UPDATE_USER_PROFILE;
  constructor(public payload:any){}
}

export class deleteUserAppointment implements Action{
  readonly type = DELETE_USER_APPOINTMENT;
  constructor(public payload:any){}
}

export class returnToUserProfile implements Action{
  readonly type = USER_PROFILE;
  constructor(){}
}

export class logoutProfile implements Action{
  readonly type = LOGOUT_PROFILE;
  constructor(){}
}

export class logoutProfileSuccess implements Action{
  readonly type = LOGOUT_PROFILE_SUCCESS;
  constructor(){}
}

export class logoutProfileFailed implements Action{
  readonly type = LOGOUT_PROFILE_FAILED;
  constructor(public payload){}
}

export class viewProfileAppointments implements Action{
  readonly type = VIEW_PROFILE_APPOINTMENTS;
  constructor(){}
}

export class getProfileSuccess implements Action{
  readonly type = GET_PROFILE_SUCCESS;
  constructor(public payload:any){}
}

export class getProfileFailed implements Action{
  readonly type = GET_PROFILE_FAILED
  constructor(public payload:any){}
}


export type profileActions = logoutProfileFailed| logoutProfileSuccess| logoutProfile| updateUserProfile | deleteUserAppointment | viewProfileAppointments| returnToUserProfile| getProfile | getProfileFailed | getProfileSuccess;
