import { Injectable } from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, tap, exhaustMap} from 'rxjs/operators';
import {of} from 'rxjs/observable/of';
import {Router} from '@angular/router';
import { Store } from '@ngrx/store';
import {AuthService, ShopService, ClientService} from '../service';
import { PAY_APPOINTMENT, payAppointment, payAppointmentSuccess, payAppointmentFailed, PAY_APPOINTMENT_SUCCESS } from '../actions';


@Injectable()
export class PaymentEffects{


@Effect()
payForServiceEffect$ = this.actions$.pipe(
ofType(PAY_APPOINTMENT),
mergeMap((action:payAppointment) => this.clientService.payService(action.payload).pipe(
    map((respond) =>{ return new payAppointmentSuccess(respond)}),
    catchError((respond) => of(new payAppointmentFailed(respond)))
    )
  )
);


@Effect({dispatch:false})
  clientPaymentSuccess$ = this.actions$.pipe(
    ofType(PAY_APPOINTMENT_SUCCESS),
    mergeMap(() => this.router.navigate(['pay-success']))
  );

















    constructor(private store:Store<any>, private actions$: Actions, private shopService: ShopService,private router: Router, private authService: AuthService, private clientService:ClientService ) {}
}