import { addBusinesHoursInfo } from './../../actions/business.actions';
import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { CustomValidator } from 'src/app/utilities/custom-validations';
import { businessHours } from 'src/app/models';

@Component({
  selector: 'app-store-hours',
  templateUrl: './store-hours.component.html',
  styleUrls: ['./store-hours.component.css']
})
export class StoreHoursComponent implements OnInit {

  show:boolean;
  formError:string;
  storeHours:businessHours;
  storeHourForm:FormGroup;
  constructor(private fb:FormBuilder,private store:Store<any>) { }

  ngOnInit() {
    this.show = false;
    this.createStoreHourForm();
  }

  createStoreHourForm(){
    this.storeHourForm = this.fb.group({
      dayFrom0: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom1: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom2: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom3: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom4: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom5: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom6: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo0: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo1: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo2: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo3: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo4: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo5: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo6: new FormControl('',Validators.compose([Validators.required, CustomValidator.hoursValidator]))
    })
  }

  addStoreHours(){
    if(this.storeHourForm.invalid){
      this.show = true;
      this.formError = 'Please complete form';
    }
    this.storeHours = this.storeHourForm.value;
    this.store.dispatch(new addBusinesHoursInfo(this.storeHours));
  }

}
