import { createBusinessAccount } from '../../actions/business.actions';
import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { ApplicationState } from 'src/app/reducers';
@Component({
  selector: 'app-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.css']
})
export class ConfirmationComponent implements OnInit {
  showClient:Boolean;
  showPayment:Boolean;
  showBusiness:Boolean;
  constructor(private store:Store<any>) { }

  ngOnInit() {
    this.store.select('app').subscribe((state:ApplicationState)=>{
      if(state.business.businessName !== null){
        this.showBusiness = state.business.businessName !== null ? true : false;
      }else if(state.clientAccount.email !== null){
        this.showClient = state.clientAccount.email !== null ? true : false;
      }else{
        return;
      }
    });
  }

}
