import { Store } from '@ngrx/store';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { addBusinesServicesInfo } from '../../actions/business.actions';
import {  Services } from '../../models/barber.model';

@Component({
  selector: 'app-business-services',
  templateUrl: './business-services.component.html',
  styleUrls: ['./business-services.component.css']
})
export class BusinessServicesComponent implements OnInit {

  businessServicesForm:FormGroup;
  serviceDetails:Services;

  constructor(private store:Store<any>, private fb:FormBuilder) { }

  ngOnInit() {
    this.createServicesForm();
  }

  createServicesForm(){
    this.businessServicesForm =  this.fb.group({
     haircut: new FormControl('', Validators.required),
     lineUp: new FormControl('', Validators.required),
     hairdye: new FormControl('', Validators.required),
     facialTrim: new FormControl('', Validators.required),
   })
 }

 addServicePrices(){
   this.serviceDetails = {
     haircut:this.businessServicesForm.controls.haircut.value,
     lineUp:this.businessServicesForm.controls.lineUp.value,
     hairdye:this.businessServicesForm.controls.hairdye.value,
     facialTrim:this.businessServicesForm.controls.facialTrim.value,
    };
   this.store.dispatch(new addBusinesServicesInfo(this.serviceDetails));
 }

}
