import { Action } from '@ngrx/store';

export const LOGIN_USER = 'LOGIN_USER';
export const LOGIN_INVALID_CREDENTIALS = 'LOGIN_INVALID_CREDENTIALS';
export const LOGIN_USER_FAILED = 'LOGIN_USER_FAILED';
export const LOGIN_USER_SUCCESS = 'LOGIN_USER_SUCCESS';
export const LOGIN_PROFILE = 'LOGIN_PROFILE';
export const LOGIN_PROFILE_FAILED = 'LOGIN_PROFILE_FAILED';
export const LOGIN_PROFILE_SUCCESS = 'LOGIN_PROFILE_SUCCESS';

export class loginProfileTransaction implements Action{
  readonly type = LOGIN_PROFILE;
  constructor(public payload:any){}
}

export class loginInvalidCredentials implements Action{
  readonly type = LOGIN_INVALID_CREDENTIALS;
  constructor(public payload:any){}
}

export class loginProfileSuccess implements Action{
  readonly type = LOGIN_PROFILE_SUCCESS;
  constructor(public payload:any){}
}

export class loginProfileFailed implements Action{
  readonly type = LOGIN_PROFILE_FAILED
  constructor(public payload:any){}
}

export class loginUserTransaction implements Action{
  readonly type = LOGIN_USER;
  constructor(public payload:any){}
}

export class loginUserSuccess implements Action{
  readonly type = LOGIN_USER_SUCCESS;
  constructor(public payload:any){}
}

export class loginUserFailed implements Action{
  readonly type = LOGIN_USER_FAILED
  constructor(public payload:any){}
}




export type loginActions = loginInvalidCredentials | loginUserTransaction | loginUserSuccess | loginUserFailed | loginProfileTransaction | loginProfileFailed | loginProfileSuccess;
