
import { Store } from '@ngrx/store';
import { Component, OnInit } from '@angular/core';
import { ShopProfiles } from 'src/app/models';
import { bookAppointment, viewShop, searchShop, fetchAllBusinessessSuccess } from 'src/app/actions';
import { ApplicationState } from 'src/app/reducers';
import { SessionService } from 'src/app/service/session-service';

@Component({
  selector: 'app-shop',
  templateUrl: './shop.component.html',
  styleUrls: ['./shop.component.css']
})
export class ShopComponent implements OnInit {

  shopDetails:ShopProfiles[];
  comma:string;
  constructor(private store:Store<any>,private session:SessionService) { }

  ngOnInit() {
    this.store.select('app').subscribe((data:ApplicationState)=>{
      if(data.shop && data.shop.loaded ){
        this.comma = ", ";
        this.shopDetails = data.shop.storeData;
      }else{
        this.session.retriveAllShopsFromSession();
      }    
    });
  }


  viewShop(id:string){
    this.store.dispatch(new viewShop(id));
  }

  onSearchChange(event:any){
    const searchValue:any = document.getElementById('searchField');
    if(searchValue.value !== ''){
      const data = {
        city:searchValue.value
      }
     this.store.dispatch(new searchShop(data));
    }else{
      return;
    }
    
  }
  

}
