import { Injectable } from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, tap} from 'rxjs/operators';
import { ShopService} from '../service';
import {of} from 'rxjs/observable/of';
import {Router} from '@angular/router';
import { Store, Action } from '@ngrx/store';
import { FETCH_ALL_SHOP, fetchAllBusinessessSuccess, fetchAllBusinessessFailed, SEARCH_SHOP, searchShop } from '../actions';

@Injectable()
export class ShopEffects {

constructor(private store:Store<any>, private actions$: Actions, private shopService: ShopService,private router: Router ) {}

@Effect()
fetchBusinesses$ = this.actions$.pipe(
ofType(FETCH_ALL_SHOP),
mergeMap((action) => this.shopService.getAllShops().pipe(
    map((data:any) =>  {          
        if(data){
        this.router.navigate(['shops']);
        sessionStorage.setItem('SHOPS',JSON.stringify(data));
        return  new fetchAllBusinessessSuccess(data)
        }
    }),
    catchError((err) => of(new fetchAllBusinessessFailed(err)))
    )
    ),
);

@Effect()
searchShop$ = this.actions$.pipe(
ofType(SEARCH_SHOP),
mergeMap((action:searchShop) => this.shopService.searchShop(action.payload).pipe(
    map((data:any) =>  {    
        debugger;      
        if(data){
            this.router.navigate(['shops']);
            return  new fetchAllBusinessessSuccess(data)
        }
    }),
    catchError((err) => of(new fetchAllBusinessessFailed(err)))
    )
    ),
);


}