import { Component, OnInit, Input } from '@angular/core';
import { Store } from '@ngrx/store';
import { bookAppointment } from '../../actions/booking.actions';
import { editBusinessInfo, viewProfileAppointments, viewMyProfile, logoutProfile, viewProfile } from 'src/app/actions';
import { profileState } from 'src/app/models';
import { ApplicationState } from 'src/app/reducers';
import { SessionService } from 'src/app/service/session-service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  showHeader:boolean;
  profileDetails:profileState;
  constructor(private store:Store<any>, private session:SessionService) {}

  ngOnInit() {
    this.showHeader = false;
    this.store.select('app').subscribe((state:ApplicationState)=>{
      if(state.profile.loaded && state.profile.contactInfo !== null){
        this.profileDetails = state.profile;
        this.showHeader = true;
      }
    });
  }

  editProfile(){
    this.store.dispatch(new editBusinessInfo());
  }

  viewProfile(){
    this.store.dispatch(new viewProfile());
  }


  viewAppointments(){
    this.store.dispatch(new viewProfileAppointments() );
  }

  logoutShop(){
    this.showHeader = false;
    this.session.clearSessionData();
    this.store.dispatch(new logoutProfile());
  }

}
