import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { ApplicationState } from 'src/app/reducers';
import { profileState } from 'src/app/models';
import { deleteUserAppointment, getProfileSuccess } from 'src/app/actions';
import { SessionService } from 'src/app/service/session-service';


@Component({
  selector: 'app-business-appointments',
  templateUrl: './business-appointments.component.html',
  styleUrls: ['./business-appointments.component.css']
})
export class BusinessAppointmentsComponent implements OnInit {
  profileDetails:profileState;
  constructor(private store:Store<any>, private session: SessionService) {}

  ngOnInit() {

    const sessionData = sessionStorage.getItem('BUSINESS_USER');
    this.store.select('app').subscribe((state:ApplicationState)=>{
      if(state.profile.loaded && state.profile){
        this.profileDetails = state.profile;
      }else{
        this.session.retriveShopSession();
      }
    });
  }

  deleteAppointment(id){
    this.store.dispatch(new deleteUserAppointment(id))
  }

}
