import { Injectable } from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, tap, exhaustMap} from 'rxjs/operators';
import {of} from 'rxjs/observable/of';
import {Router} from '@angular/router';
import { Store } from '@ngrx/store';
import {AuthService, ShopService, ClientService} from '../service';
import { CREATE_APPOINTMENT, createAppointment, createAppointmentSuccess, BOOK_APPOINTMENT, bookAppointment } from '../actions/booking.actions';

@Injectable()
export class BookingEffects{


    @Effect({dispatch:false})
    CreateAppointment$ = this.actions$.pipe(
      ofType(CREATE_APPOINTMENT),
      mergeMap((action:createAppointment) => {
        return this.clientService.saveAppointment(action.payload).pipe(
          map(() => this.router.navigate(['pay'])),
        ); 
      }),
    );

    @Effect({dispatch:false})
    BookAppointment$ = this.actions$.pipe(
      ofType(BOOK_APPOINTMENT),
      mergeMap((action:bookAppointment) => {
        sessionStorage.setItem('BOOKINGSTORE',JSON.stringify(action.payload));
        return this.router.navigate(['book-appointment'])}
        ),
    );

















    constructor(private store:Store<any>, private actions$: Actions, private shopService: ShopService,private router: Router, private authService: AuthService, private clientService:ClientService ) {}
}