import { Component, OnInit, HostListener } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { Customer, ClientAccountState } from 'src/app/models';
import { UpdateClient, getProfile, returnToUserProfile } from 'src/app/actions';
import { ApplicationState } from 'src/app/reducers';
import { Observable } from 'rxjs';
import { DialogService } from 'src/app/service/dialog.service';


@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {

  ClientContactForm:FormGroup;
  profileUpdate:ClientAccountState;
  ClientAccount:ClientAccountState;

  constructor(private store:Store<any>,private fb:FormBuilder, private dialogService:DialogService) { }

  ngOnInit() {
    this.store.select('app').subscribe((data:ApplicationState)=>{
      if(data.client && data.client.loaded){
        this.profileUpdate = data.client;
        this.createContactForm(this.profileUpdate);
      }
      
    })
    
  }

 

@HostListener('window:beforeunload')
alertMessage(){
  return window.confirm('');
} 


 canDeactivate():Observable<boolean> | boolean{
    return this.dialogService.confirm();
 }

 createContactForm(data){
   this.ClientContactForm = this.fb.group({
     firstName: new FormControl(data.firstName, Validators.required),
     lastName: new FormControl(data.lastName, Validators.required),
     email: new FormControl(data.email, Validators.required),
     password: new FormControl('', Validators.required),
     phoneNumber: new FormControl(data.phoneNumber, Validators.required),
   })
 }

  addContact(){
    this.ClientAccount = {
      firstName:this.ClientContactForm.controls.firstName.value,
      lastName:this.ClientContactForm.controls.lastName.value,
      email:this.ClientContactForm.controls.email.value,
      password:this.ClientContactForm.controls.password.value,
      phoneNumber:this.ClientContactForm.controls.phoneNumber.value,
      id:this.profileUpdate.id
      };
    this.store.dispatch(new UpdateClient(this.ClientAccount));
  }

  viewProfile(){
    this.store.dispatch(new returnToUserProfile());
  }

}
