
import { addBusinesPersonalInfo } from '../../actions/business.actions';
import { Store } from '@ngrx/store';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Personal } from 'src/app/models';

@Component({
  selector: 'app-personal',
  templateUrl: './personal.component.html',
  styleUrls: ['./personal.component.css']
})
export class PersonalComponent implements OnInit {
  businessPersonalForm:FormGroup;
  businessPersonalModel:Personal;
  constructor(private store:Store<any>, private fb:FormBuilder) { }

  ngOnInit() {
    this.businessPersonalForm = this.createPersonalForm();
  }

  createPersonalForm(){
    return this.fb.group({
     email: new FormControl('', [Validators.required, Validators.email]),
     shopName: new FormControl('', Validators.required),
     password: new FormControl('', Validators.required),
   })
 }

  addPersonalInfo(){
    this.businessPersonalModel = {
      businessName:this.businessPersonalForm.controls.shopName.value,
      email:this.businessPersonalForm.controls.email.value,
      password:this.businessPersonalForm.controls.password.value
    }
    this.store.dispatch(new addBusinesPersonalInfo(this.businessPersonalModel));
  }

}
