import { Component, OnDestroy, AfterViewInit, AfterContentInit, Input, OnInit, OnChanges } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import {ClientService} from '../../service/client.service';
import { Elements, StripeService} from 'ngx-stripe';
import {Router} from "@angular/router";
import { payAppointment } from 'src/app/actions';
import { purchaseData, Appointment} from '../../models/index';
import { ApplicationState } from 'src/app/reducers';


@Component({
  selector: 'app-pay',
  templateUrl: './pay.component.html',
  styleUrls: ['./pay.component.css']
})
export class PayComponent implements OnInit, OnDestroy,AfterViewInit, OnChanges {


  constructor(private store:Store<any>,private fb:FormBuilder, private stripeService:StripeService,
    private clientService:ClientService, private router:Router) { }

  bookingData:Appointment;
  elements: Elements;
  card: any;
  error:string;
  buttonValid:boolean = false;
  paymentData:purchaseData;

  ngOnInit(){
    this.store.select('app').subscribe((data:ApplicationState)=>{
      if(data.booking.loaded && data.booking.bookingInfo){
        this.bookingData = data.booking.bookingInfo;
      }     
    });
 
  }

  

  ngAfterViewInit(){
    this.stripeService.elements().subscribe(elements => {
      this.elements = elements;
      // Only mount the element the first time
      if (!this.card) {
        this.card = this.elements.create('card', {
          style: {
            base: {
              iconColor: '#666EE8',
              color: '#31325F',
              lineHeight: '40px',
              fontWeight: 300,
              fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
              fontSize: '18px',
              '::placeholder': {
                color: '#CFD7E0'
              }
            }
          },
          hidePostalCode:true
        });
        this.card.mount('#card-element');
      }

      
      this.card.addEventListener('change', function(event) {
        const displayError = document.getElementById('card-errors');
        if (event.error) {
          displayError.setAttribute("class","alert alert-warning");
          displayError.textContent = event.error.message;
        } else {
          displayError.removeAttribute("class");
          displayError.textContent = null;
        }
      });
    });
  }

  ngOnChanges(){
    this.card.on('blur',(event) => { console.log('hello')});
  }


  buy() {
    this.stripeService
      .createToken(this.card,undefined)
      .subscribe(result => {
        if (result.token) {
          this.paymentData = {
            amount:this.bookingData.balance,
            token:result.token,
            currency:'usd'
          }
          this.store.dispatch(new payAppointment(this.paymentData))
        } else if (result.error) {
          const errorElement = document.getElementById('card-errors');
          errorElement.textContent = result.error.message;
        }
      });
  } 



  ngOnDestroy() {
    this.card.destroy();
  }

}
