import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';


@Injectable()
export class ClientService {
  authToken:any;

  constructor(private _http: HttpClient) { }

  HEADERS = new HttpHeaders({
    'Content-Type': 'application/json'
  });

  headers_ = new HttpHeaders();

  fetchClients() {
    return this._http.get("/Client",{headers:this.HEADERS,observe:'response'});
  }

  retrieveProfile(id){
    return this._http.get("/Client/"+id,{headers:this.HEADERS,observe:'response'});
  }

  getUserDetails(){
      return this._http.get('/Client/GetProfileDetails');
  }


  updateClient(data:any){
    return this._http.put("/Client/UpdateClient",data)
  }

  deleteAppointment(id:any){
    return this._http.delete("/Client/CancelMyAppointment/"+id);
  }

  logMeOut(){
    return this._http.get("/Client/LogoutUser");
  }

  updateMyAppointment(id:any){
    return this._http.put("/Client/UpdateMyAppointment",id);
  }

  saveClient(data:any){
    return this._http.post("/Client/CreateClient",data,{headers:this.HEADERS, observe:'response'});
  }

  sendClientData(data:any){
    return this._http.post("/Client/CreateEmail",data,{headers:this.HEADERS,observe:'response'});
  }


  login(data:any,options?:any){
    return this._http.post("/Client/Login",data,{headers:this.HEADERS,observe:'response'});
  }

  fetchToken(){
    return this._http.get("/Client/Token",{headers:this.HEADERS,observe:'response'}).map((response)   =>{return response} );
  }

  payService(data:any){
    return this._http.post("/Client/Pay",data,{headers:this.HEADERS,observe:'response'});
  }

  saveAppointment(data:any){
    return this._http.post("/Client/SaveAppointment",data);
  }

  findBarberInCity(data:any){
    return this._http.post("/Client/FindBarber",data);
  }

  updateClientDetails(data:any){
    return this._http.put("/Client/UpdateClient",data);
  }


  storeUserData(token) {
    localStorage.setItem('jwt_token', token);
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }


  logoutUser(){
    localStorage.clear();
  }

}

