import { AppRouteService } from '../service/app-route.service';
import { Injectable } from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap} from 'rxjs/operators';
import {of} from 'rxjs/observable/of';
import { Store } from '@ngrx/store';
import {Router} from '@angular/router';
import { ADD_CLIENT_INFO, addClientAccount, addClientAccountSuccess, addClientAccountFailed, ADD_CLIENT_INFO_SUCCESS, GET_CLIENT_PROFILE, getClientAccountSuccess, getClientAccountFailed, EDIT_CLIENT_INFO, VIEW_APPOINTMENTS, UPDATE_CLIENT, UpdateClient, getClientAccount, UpdateClientSuccess, UpdateClientFailed, VIEW_SHOP, viewShop, UPDATE_APPOINTMENT, EDIT_APPOINTMENT, updateAppointment, cancelAppointment, CANCEL_APPOINTMENT, VIEW_MY_PROFILE, LOGOUT_USER, logoutUser, logoutUserSuccess, GET_CLIENT_PROFILE_SUCCESS } from '../actions/client.actions';
import { ClientService } from '../service/client.service';
import { fetchShop } from '../actions';



@Injectable()
export class ClientEffects {

  constructor(private store:Store<any>, private actions$: Actions,private router: Router ,private clientService: ClientService) {}


  //Client Routing
  @Effect({dispatch:false})
  addClientSuccess$ = this.actions$.pipe(
    ofType(ADD_CLIENT_INFO_SUCCESS),
    mergeMap(() => this.router.navigate(['confirmation']))
  );

  @Effect({dispatch:false})
  editClientInfo$ = this.actions$.pipe(
    ofType(EDIT_CLIENT_INFO),
    mergeMap(() => this.router.navigate(['edit-client']))
  );

  @Effect({dispatch:false})
  viewMyProfile$ = this.actions$.pipe(
    ofType(VIEW_MY_PROFILE),
    mergeMap(() => this.router.navigate(['user-profile']))
  );

  @Effect({dispatch:false})
  editClientAppointments$ = this.actions$.pipe(
    ofType(EDIT_APPOINTMENT),
    mergeMap(() => this.router.navigate(['edit-appointment']))
  );

  @Effect({dispatch:false})
  viewClientAppointments$ = this.actions$.pipe(
    ofType(VIEW_APPOINTMENTS),
    mergeMap(() => this.router.navigate(['user-appointments']))
  );

  @Effect({dispatch:false})
  getClientProfileSuccess$ = this.actions$.pipe(
    ofType(GET_CLIENT_PROFILE_SUCCESS),
    mergeMap(() => this.router.navigate(['user-profile']))
  );

  @Effect({dispatch:false})
  viewShopProfile$ = this.actions$.pipe(
    ofType(VIEW_SHOP),
    mergeMap((action:viewShop) => {
      sessionStorage.setItem('VIEWSHOPID',JSON.stringify(action.payload));
      this.store.dispatch(new fetchShop(action.payload))
      return this.router.navigate(['view-shop']);
    })
  );



  //Client Side Effects
  @Effect()
  addClientInfo$ = this.actions$.pipe(
  ofType(ADD_CLIENT_INFO),
  mergeMap((action:addClientAccount) => this.clientService.saveClient(action.payload).pipe(
      map((response:any) => {
          return new addClientAccountSuccess(response)
      }),
      catchError((respond) => of(new addClientAccountFailed(respond)))
      )
    )
  );

  @Effect()
  getClientProfile$ = this.actions$.pipe(
  ofType(GET_CLIENT_PROFILE),
  mergeMap(action => this.clientService.getUserDetails().pipe(
      map((data) => {
        sessionStorage.setItem('USER',JSON.stringify(data));
        return new getClientAccountSuccess(data)
      }),
      catchError((err) => of(new getClientAccountFailed(err)))
    )
  ),
);

@Effect()
  updateClientProfile$ = this.actions$.pipe(
  ofType(UPDATE_CLIENT),
  mergeMap((action:UpdateClient) => this.clientService.updateClient(action.payload).pipe(
      map((data:any) => {
        if(data.success){
          this.store.dispatch(new getClientAccount());
          return new UpdateClientSuccess(data);
        }
      }),
      catchError((err) => of(new UpdateClientFailed(err)))
    )
  ),
);

@Effect()
  logoutClientProfile$ = this.actions$.pipe(
  ofType(LOGOUT_USER),
  mergeMap((action:logoutUser) => this.clientService.logMeOut().pipe(
      map((data:any) => {
        if(data.success){
          
          this.router.navigate(['login']);
          return new logoutUserSuccess();
        }
      }),
      catchError((err) => of(new UpdateClientFailed(err)))
    )
  ),
);

@Effect()
  updateMyAppointment$ = this.actions$.pipe(
  ofType(UPDATE_APPOINTMENT),
  mergeMap((action:updateAppointment) => this.clientService.updateMyAppointment(action.payload).pipe(
      map((data:any) => {
        if(data.success){
          this.store.dispatch(new getClientAccount());
          this.router.navigate(['user-profile']);
          return new UpdateClientSuccess(data);
        }
      }),
      catchError((err) => of(new UpdateClientFailed(err)))
    )
  ),
);

@Effect()
  deleteMyAppointment$ = this.actions$.pipe(
  ofType(CANCEL_APPOINTMENT),
  mergeMap((action:cancelAppointment) => this.clientService.deleteAppointment(action.payload).pipe(
      map((data:any) => {
        if(data.success){
          this.store.dispatch(new getClientAccount());
          this.router.navigate(['user-profile']);
          return new UpdateClientSuccess(data);
        }
      }),
      catchError((err) => of(new UpdateClientFailed(err)))
    )
  ),
);


}

