import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { addBusinesContactInfo } from '../../actions/business.actions';
import { Store } from '@ngrx/store';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import {  Address, Contact } from '../../models/barber.model';

@Component({
  selector: 'app-business-account',
  templateUrl: './business-account.component.html',
  styleUrls: ['./business-account.component.css']
})
export class BusinessAccountComponent implements OnInit {
  businessContactForm:FormGroup;
  businessAccount:Contact;

  constructor(private store:Store<any>,private fb:FormBuilder) { }

  ngOnInit() {
   this.createContactForm();
  }

 createContactForm(){
   this.businessContactForm = this.fb.group({
     firstName: new FormControl('', Validators.required),
     lastName: new FormControl('', Validators.required),
     phoneNumber: new FormControl('', Validators.required),
     businessAddress: this.fb.group({
      street_number: new FormControl('', Validators.required),
      route: new FormControl('', Validators.required),
      locality: new FormControl('', Validators.required),
      administrative_area_level_1: new FormControl('', Validators.required),
      country: new FormControl('', Validators.required),
      postal_code: new FormControl('', Validators.required),
    }),
   })
 }

  addBusinessContact(){
    this.businessAccount = {
      firstName:this.businessContactForm.controls.firstName.value,
      lastName:this.businessContactForm.controls.lastName.value,
      phoneNumber:this.businessContactForm.controls.phoneNumber.value,
      address_:this.businessContactForm.controls.businessAddress.value,
    };
    this.store.dispatch(new addBusinesContactInfo(this.businessAccount));
  }

}
