import { UPDATE_APP_STATE } from "../actions/application.actions";
import { LOGIN_USER_SUCCESS, LOGIN_INVALID_CREDENTIALS, LOGIN_USER_FAILED, LOGIN_PROFILE_SUCCESS, LOGIN_PROFILE_FAILED, BOOK_APPOINTMENT, CREATE_APPOINTMENT, CREATE_APPOINTMENT_SUCCESS, CREATE_APPOINTMENT_FAILED, ADD_BUSINESS_PERSONAL_INFO, ADD_BUSINESS_CONTACT_INFO, ADD_BUSINESS_SERVICES, ADD_BUSINESS_HOURS, PAY_APPOINTMENT_SUCCESS, PAY_APPOINTMENT, PAY_APPOINTMENT_FAILED, GET_PROFILE_SUCCESS, GET_PROFILE_FAILED, FETCH_ALL_SHOP_SUCCESS, FETCH_ALL_SHOP_FAILED, FETCH_SHOP, GET_CLIENT_PROFILE_SUCCESS, GET_CLIENT_PROFILE_FAILED, LOGOUT_USER_SUCCESS, LOGOUT_PROFILE_SUCCESS, ADD_CLIENT_INFO, UPDATE_CLIENT_SUCCESS, UPDATE_CLIENT_FAILED, UPDATE_BUSINESS_ACCOUNT_SUCCESS, UPDATE_BUSINESS_ACCOUNT_FAILED, BOOK_APPOINTMENT_SUCCESS } from "../actions";
import { LoginState, ClientAccountState, CreateBusinessState, ClientBookingState, PaymentState, profileState, shopState, createClientState } from "../models";



export interface ApplicationState{
    sessionId:any;
    login:LoginState;
    client:ClientAccountState;
    business:CreateBusinessState;
    booking:ClientBookingState;
    payment:PaymentState;
    profile:profileState;
    clientAccount:createClientState;
    shop:shopState;
}

export const initialApplicationState:ApplicationState = {
    sessionId:null,
    login:{
      loaded:false,
      message:null,  
    },
    client:{
      id:null,
      firstName:null,
      lastName:null,
      email:null,
      password:null,
      phoneNumber:null,
      appointment:null,
      loaded:false,
    },
    clientAccount:{
      firstName:null,
      lastName:null,
      email:null,
      password:null,
      phone:null,
    },
    business:{
      email:null,
      password:null,
      businessName:null,
      appointments:[],
      id:null,
      _id:null,
      contactInfo:null,
      serviceInfo:null,
      businessHours:null   
    },
    booking:{
      loaded:false,
      bookingInfo:null
    },
    payment:{
      success:null,
      cardData:null,
    },
    profile:{
      loaded:false,
      email:null,
      businessName:null,
      appointments:[],
      id:null,
      contactInfo:null,
      serviceInfo:null,
      businessHours:null
    },
    shop:{
      storeData:[]
    }
}


const getStore = function findStoreById(data:Array<any>,id:string){
  
  const profile = data.filter((elem) => elem._id === id);
  return profile
}

export function applicatonReducer(state = initialApplicationState, action ):ApplicationState {
    switch (action.type) {
      case LOGIN_PROFILE_SUCCESS:
      return {...state, 
        login:{
          loaded:true, 
          message: action.payload
          }
      }
      case LOGIN_USER_SUCCESS:{
        return {...state, 
          login:{
            loaded:true, 
            message: action.payload
            }
          }
      }
      case LOGIN_INVALID_CREDENTIALS:{
        return {...state,  
          login:{
            loaded:false,
            message: action.payload
          }
        }
      }
      case LOGIN_PROFILE_FAILED:
      case LOGIN_USER_FAILED:{
        return {...state, 
          login:{
            loaded:false, 
            message:action.payload            
        }
       }
      }
      case BOOK_APPOINTMENT:{
        
        return {...state, 
          booking:{ 
            loaded:true,
            bookingInfo:action.payload 
          }
        }
      }
      case BOOK_APPOINTMENT_SUCCESS:{
        
        return {...state, 
          booking:{ 
            loaded:true,
            bookingInfo:action.payload 
          }
        }
      }
      case CREATE_APPOINTMENT:{
        return {...state, 
          booking:{ 
            loaded:true,
            bookingInfo:action.payload 
          }
        }
      }
      case ADD_CLIENT_INFO:{
        return {...state, 
          clientAccount:{ 
            firstName:action.payload.firstName,
            lastName:action.payload.lastName,
            email:action.payload.email,
            phone:action.payload.phone,
            password:action.payload.password
          }
        }
      }
      case CREATE_APPOINTMENT_SUCCESS:{
        return {...state, 
          booking:{ 
            loaded:true,
            bookingInfo:action.payload 
          }
        }
      }
      case CREATE_APPOINTMENT_FAILED:{
        return {...state, 
          booking:{ 
            loaded:false,
            bookingInfo:action.payload 
          }
        }
      }
      case ADD_BUSINESS_PERSONAL_INFO:{
        return {...state, 
          business:{
            password: action.payload.password,
            email: action.payload.email, 
            businessName:action.payload.businessName
          }, 
          
        }
      }
      case ADD_BUSINESS_CONTACT_INFO:{
        
        return {...state, 
          business:{
            password: state.business.password,
            email: state.business.email, 
            businessName:state.business.businessName,
            contactInfo: action.payload
          }
        }
      }
      case ADD_BUSINESS_SERVICES:{
        
        return {...state,
          business:{
            password: state.business.password,
            email: state.business.email, 
            businessName:state.business.businessName,
            contactInfo: state.business.contactInfo,
            serviceInfo: action.payload
          }
        } 
      }
      case ADD_BUSINESS_HOURS:{
        
        return {...state, 
          business:{
            password: state.business.password,
            email: state.business.email, 
            businessName:state.business.businessName,
            contactInfo: state.business.contactInfo,
            serviceInfo: state.business.serviceInfo,
            businessHours: action.payload
         } 
        }
      }
      case PAY_APPOINTMENT:{
        return {...state,
          payment:{
          success:false,
          cardData:action.payload
         } 
       }
      }
      case PAY_APPOINTMENT_SUCCESS:{
        return {...state,
          payment:{
            success:true,
            cardData:action.payload
         } 
        }
      }
      case PAY_APPOINTMENT_FAILED:{
        return {...state,
          payment:
          {
            success:false,
            cardData:action.payload
          }
        }
      }
      case GET_CLIENT_PROFILE_SUCCESS:{
        
        return {...state, 
          client:
          {
            loaded:true,
            email:action.payload.email, 
            id:action.payload._id,
            firstName:action.payload.firstName,
            lastName:action.payload.lastName,
            phoneNumber:action.payload.phoneNumber,
            appointment:action.payload.appointment
          }
          
        }
      }
      case UPDATE_CLIENT_SUCCESS:{
        
        return {...state, 
          client:
          {
            loaded:true,
            email:action.payload.email, 
            id:action.payload._id,
            firstName:action.payload.firstName,
            lastName:action.payload.lastName,
            phoneNumber:action.payload.phoneNumber,
            appointment:action.payload.appointment
          }
          
        }
      }
      case UPDATE_CLIENT_FAILED:{
        
        return {...state, 
          client:
          {
            loaded:false,
            email:action.payload.email, 
            id:action.payload._id,
            firstName:action.payload.firstName,
            lastName:action.payload.lastName,
            phoneNumber:action.payload.phoneNumber,
            appointment:action.payload.appointment
          }
          
        }
      }
      case LOGOUT_USER_SUCCESS:{
        
        return {...state, 
          client:
          {
            loaded:false,
            email:null, 
            id:null,
            firstName:null,
            lastName:null,
            phoneNumber:null,
            appointment:null
          },
          login:{
            message:null,
            loaded:false
          }
          
        }
      }
      case LOGOUT_PROFILE_SUCCESS:{
        
        return {...state, 
          profile:{
            loaded:false,
            email:null,
            businessName:null,
            appointments:[],
            id:null,
            contactInfo:null,
            serviceInfo:null,
            businessHours:null
          },
          login:{
            message:null,
            loaded:false
          }
          
        }
      }
      case GET_CLIENT_PROFILE_FAILED:{
        
        return {...state,  
          client:
          {
            loaded:false,
            email:action.payload.email, 
            id:action.payload._id,
            firstName:action.payload.firstName,
            lastName:action.payload.lastName,
            phoneNumber:action.payload.phoneNumber
          }
        }
      }
      case GET_PROFILE_SUCCESS:{
        
        return {...state, 
          profile:{
            loaded:true, 
            email:action.payload.email, 
            contactInfo:action.payload.contactInfo,
            id:action.payload._id,
            serviceInfo:action.payload.serviceInfo,
            businessHours:action.payload.businessHours,
            appointments:action.payload.appointments,
            businessName:action.payload.businessName
          }          
        }
      }
      case UPDATE_BUSINESS_ACCOUNT_SUCCESS:{
        
        return {...state, 
          profile:{
            loaded:true, 
            email:action.payload.email, 
            contactInfo:action.payload.contactInfo,
            id:action.payload._id,
            serviceInfo:action.payload.serviceInfo,
            businessHours:action.payload.businessHours,
            appointments:action.payload.appointments,
            businessName:action.payload.businessName
          }          
        }
      }
      case UPDATE_BUSINESS_ACCOUNT_FAILED:
      case GET_PROFILE_FAILED:{
        
        return {...state,
          profile:{
            loaded:false, 
            email:action.payload.email, 
            contactInfo:action.payload.contactInfo,
            id:action.payload._id,
            serviceInfo:action.payload.serviceInfo,
            businessHours:action.payload.businessHours,
            businessName:action.payload.businessName,
            appointments:[...action.payload.appointments]
          },
        }
      }case FETCH_ALL_SHOP_SUCCESS:{
        return {...state, 
          shop:{
              loaded:true, 
              storeData: [...action.payload]
          }          
        }
      }
      case FETCH_ALL_SHOP_FAILED:{
        return {...state,  
          shop:{
            loaded:false,
            storeData: null
          } 
        }
      }
      case FETCH_SHOP:{
        
        return {...state,
          shop:{
          loaded:true,
          storeData: [...getStore(state.shop.storeData,action.payload)]
        } 
      }
      }

      case UPDATE_APP_STATE:{
        return {...state, 
            login:action.payload.login, 
            client:action.payload.client,
            business:action.payload.business,
            profile:action.payload.profile,
            booking:action.payload.booking,
            shop:action.payload.shop,
            payment:action.payload.payment
        }
      }
      default:
        return state;
    }
  }