import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { ClientAccountState } from 'src/app/models';
import { ApplicationState } from 'src/app/reducers';
import { getClientAccountSuccess } from 'src/app/actions';
import { SessionService } from 'src/app/service/session-service';


@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {
  FullName:string;
  userDetails:ClientAccountState;
  constructor(private store:Store<any>, private session:SessionService) { }

  ngOnInit() {
    this.store.select('app').subscribe((data:ApplicationState) => {
      if(data.client && data.client.loaded){
        this.userDetails = data.client;
        this.FullName = this.userDetails.firstName+' '+this.userDetails.lastName;
      }else{    
        this.session.retriveUserSession();
      }
    });
  }

 

}
