import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { businessProfileAccount } from '../models';

@Injectable()
export class ShopService {

    constructor(private _http: HttpClient) { }

    headers_ = new HttpHeaders();

    findShopById(id:string){
        return this._http.get("/Shop/GetStore/"+id,{headers:this.headers_,observe:'response'});
    }

    login(data:Object){
      return this._http.post("/Shop/Login",data,{headers:this.headers_,observe:'response'});
    }

    createStore(data:any){
        return this._http.post("/Shop/CreateStore",data,{headers:this.headers_,observe:'response'});
    }

    getProfileDetails(){
        return this._http.get<businessProfileAccount>('/Shop/GetProfileDetails');
    }

    getAllShops(){
        return this._http.get('/Shop/GetStores');
    }

    searchShop(city:string){
        return this._http.post('/Shop/FindShops',city);
    }

    updateProfile(data:any){
        return this._http.put('/Shop/UpdateProfile',data);
    }

    updateUserAppointment(data:any){
        return this._http.put('/Shop/UpdateUserAppointment',data);
    }

    deleteUserAppointment(id){
        return this._http.delete('/Shop/DeleteUserAppointment/'+id);
    }

    deleteBusinessAccount(id:string){
        return this._http.delete('/Shop/DeleteBusinessAccount/'+id);
    }

    saveShopDetails(data:any){
        return this._http.post("/Shop/CreateProfileDetails",data,{headers:this.headers_,observe:'response'});
    }

    logOutProfile(){
        return this._http.get("/Shop/LogoutUser");
      }
    
    saveImage(file:any){
        //let headers_ = new Headers();
        return this._http.post("/Shop/image",file,{headers:this.headers_,observe:'response'});
    }

    saveGallery(files:any){
    //let headers_ = new Headers();
    return this._http.post("/Shop/gallery",files,{headers:this.headers_, observe:'response'});
    }

}
