import { Action } from '@ngrx/store';

export const CREATE_APPOINTMENT = 'CREATE_APPOINTMENT';
export const BOOK_APPOINTMENT = 'BOOK_APPOINTMENT';
export const BOOK_APPOINTMENT_SUCCESS = 'BOOK_APPOINTMENT_SUCCESS';
export const CREATE_APPOINTMENT_SUCCESS = 'CREATE_APPOINTMENT_SUCCESS';
export const CREATE_APPOINTMENT_FAILED = 'CREATE_APPOINTMENT_FAILED ';


export class createAppointment implements Action{
    readonly type = CREATE_APPOINTMENT;
    constructor(public payload?:any){}
}

export class bookAppointment implements Action{
    readonly type = BOOK_APPOINTMENT;
    constructor(public payload?:any){}
}

export class bookAppointmentSuccess implements Action{
    readonly type = BOOK_APPOINTMENT_SUCCESS;
    constructor(public payload?:any){}
}

export class createAppointmentSuccess implements Action{
    readonly type = CREATE_APPOINTMENT_SUCCESS;
    constructor(public payload:any){}
}

export class createAppointmentFailed implements Action{
    readonly type = CREATE_APPOINTMENT_FAILED;
    constructor(public payload:any){}
}



export type bookingActions = bookAppointmentSuccess | bookAppointment |createAppointment | createAppointmentSuccess | createAppointmentFailed;