import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { addBusinesContactInfo } from '../../actions/business.actions';
import { Store } from '@ngrx/store';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Customer, createClientState } from '../../models';
import { addClientAccount } from '../../actions/client.actions';

@Component({
  selector: 'app-client-account',
  templateUrl: './client-account.component.html',
  styleUrls: ['./client-account.component.css']
})
export class ClientAccountComponent implements OnInit {

  ClientContactForm:FormGroup;
  ClientAccount:createClientState;

  constructor(private store:Store<any>,private fb:FormBuilder) { }

  ngOnInit() {
   this.createContactForm();
  }

 createContactForm(){
   this.ClientContactForm = this.fb.group({
     firstName: new FormControl('', Validators.required),
     lastName: new FormControl('', Validators.required),
     email: new FormControl('', Validators.required),
     password: new FormControl('', Validators.required),
     phoneNumber: new FormControl('', Validators.required),
   })
 }

  addContact(){
    this.ClientAccount = {
      firstName:this.ClientContactForm.controls.firstName.value,
      lastName:this.ClientContactForm.controls.lastName.value,
      email:this.ClientContactForm.controls.email.value,
      password:this.ClientContactForm.controls.password.value,
      phone:this.ClientContactForm.controls.phoneNumber.value
      };
    this.store.dispatch(new addClientAccount(this.ClientAccount));
  }

}
