import { Component, OnInit,  } from '@angular/core';
import { Store } from '@ngrx/store';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { loginUserTransaction, loginProfileTransaction } from '../../actions/login.actions';
import { LoginDetails, LoginState } from '../../models/barber.model';
import { ApplicationState } from 'src/app/reducers';


@Component({
  selector: 'app-home-app',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeAppComponent implements OnInit {
  loginForm:FormGroup;
  constructor(private store:Store<any>, private fb:FormBuilder) { }
  alertMessage:String;
  showAlert:Boolean;
  tokenSet:boolean=false;
  Account:LoginDetails;


  ngOnInit(){
    this.showAlert = false;
    this.loginForm = this.createLoginForm();

  }

  createLoginForm(){
    return this.fb.group({
     type: new FormControl('', { validators:Validators.required ,updateOn: 'change' }),
     email: new FormControl('', { validators:Validators.required ,updateOn: 'change' }),
     password: new FormControl('', { validators:Validators.required ,updateOn: 'change' }),
   })
 }

  login(){
    this.Account = {
      password:this.loginForm.controls.password.value,
      email:this.loginForm.controls.email.value,
      type:this.loginForm.controls.type.value,
    };
    if(this.Account.email && this.Account.password && this.Account.type === 'USER'){
      this.store.dispatch(new loginUserTransaction(this.Account));
      this.alertLoginFailed();
    }else{
      this.store.dispatch(new loginProfileTransaction(this.Account));
      this.alertLoginFailed();
    }
  }

  alertLoginFailed(){
    this.store.select('app').subscribe((res:ApplicationState)=>{
      if(res.login){
        this.showAlert = true;
        this.alertMessage = res.login.message;
      } 
    });
  }

  onFocus(){
    this.showAlert = false;
  }


  disableEnterKey(event) {
    if (event.keyCode === 13) {
        event.preventDefault();
      return false;
    }
  }

}
