import { Component, OnInit } from '@angular/core';
import {Subscription} from 'rxjs';
import { Store } from '@ngrx/store';
import { bookAppointment } from 'src/app/actions';
import { ApplicationState } from 'src/app/reducers';
import { SessionService } from 'src/app/service/session-service';

@Component({
  selector: 'app-view-shop',
  templateUrl: './view-shop.component.html',
  styleUrls: ['./view-shop.component.css']
})
export class ViewShopComponent implements OnInit {
  private _subscription: Subscription;
  modalId:string;
  items:any = [];
  profileGallery:Array<Object>;
  data:any;
  starsCount:number;
  showSchedule:boolean;
  profileDetails:any;
  storeAddress:string;
  imageDataSrc:string;
  showScheduleTitle:string;
  constructor(private store:Store<any>,private session:SessionService) {}
  

  ngOnInit() {
    this.showSchedule = false;
    this.showScheduleTitle = "View Schedule";
    this.store.select('app').subscribe((state:ApplicationState)=>{
      if(state.shop && state.shop.loaded){
        this.profileDetails = state.shop.storeData[0];
        this.storeAddress = this.profileDetails.contactInfo.address_.street_number+" "+this.profileDetails.contactInfo.address_.route+" "+" "+this.profileDetails.contactInfo.address_.locality+" "+this.profileDetails.contactInfo.address_.administrative_area_level_1+", "+this.profileDetails.contactInfo.address_.country;
      }else{
          this.session.retrieveStoreFromSession();
      }

    });
  }


  showBusinessSchedule(){
    this.showSchedule = !this.showSchedule;
    this.showScheduleTitle = this.showSchedule ? "Hide Schedule":"View Schedule";
  }

  bookingAppointment(){
    this.store.dispatch(new bookAppointment(this.profileDetails));
  }
}
