import { Component, OnInit } from '@angular/core';
import {FormGroup, FormControl, FormBuilder, Validators} from '@angular/forms';
import { ShopService } from '../../service/shop.service';
import { Store } from '@ngrx/store';
import { updateBusinessAccount } from 'src/app/actions';
import { CustomValidator } from '../../utilities/custom-validations';
import { profileState } from 'src/app/models';
import { ApplicationState } from 'src/app/reducers';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
  settingForm:FormGroup;
  profileDetails:profileState;
  constructor(private fb:FormBuilder, private store:Store<any>) { }

  ngOnInit() {
     
     this.store.select('app').subscribe((state:ApplicationState)=>{
      if(state.profile.loaded && state.profile){
        this.profileDetails = state.profile;
        this.createSettingForm(this.profileDetails);
      }
    });
    
  }

  createSettingForm(data){
    this.settingForm = this.fb.group({
      businessName:new FormControl(data.businessName , Validators.required),
      password:new FormControl('', Validators.required),
      firstName: new FormControl(data.contactInfo.firstName , Validators.required),
      lastName: new FormControl(data.contactInfo.lastName , Validators.required),
      emailAddress: new FormControl(data.email, Validators.required),
      haircut: new FormControl(data.serviceInfo.haircut , Validators.required),
      facialTrim: new FormControl(data.serviceInfo.facialTrim , Validators.required),
      lineUp: new FormControl(data.serviceInfo.lineUp , Validators.required),
      hairdye: new FormControl(data.serviceInfo.hairdye , Validators.required),
      phoneNumber: new FormControl(data.contactInfo.phoneNumber , Validators.required),
      settingAddress: this.fb.group({
       street_number: new FormControl(data.contactInfo.address_.street_number , Validators.required),
       route: new FormControl(data.contactInfo.address_.route , Validators.required),
       locality: new FormControl(data.contactInfo.address_.locality , Validators.required),
       administrative_area_level_1: new FormControl(data.contactInfo.address_.administrative_area_level_1 , Validators.required),
       country: new FormControl(data.contactInfo.address_.country , Validators.required),
       postal_code: new FormControl(data.contactInfo.address_.postal_code , Validators.required),
      }),
      businessHours: this.fb.group({
      dayFrom0: new FormControl(data.businessHours.dayFrom0 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom1: new FormControl(data.businessHours.dayFrom1 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom2: new FormControl(data.businessHours.dayFrom2 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom3: new FormControl(data.businessHours.dayFrom3 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom4: new FormControl(data.businessHours.dayFrom4 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom5: new FormControl(data.businessHours.dayFrom5 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayFrom6: new FormControl(data.businessHours.dayFrom6 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo0: new FormControl(data.businessHours.dayTo0,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo1: new FormControl(data.businessHours.dayTo1 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo2: new FormControl(data.businessHours.dayTo2 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo3: new FormControl(data.businessHours.dayTo3 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo4: new FormControl(data.businessHours.dayTo4 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo5: new FormControl(data.businessHours.dayTo5 ,Validators.compose([Validators.required, CustomValidator.hoursValidator])),
      dayTo6: new FormControl(data.businessHours.dayTo6 ,Validators.compose([Validators.required, CustomValidator.hoursValidator]))
      }),
    })
  }


  updateBusinessProfile(){
    this.profileDetails = {
      businessName:this.settingForm.controls.businessName.value,
      password:this.settingForm.controls.password.value,
      id:this.profileDetails.id,
      contactInfo:{
        firstName:this.settingForm.controls.firstName.value,
        lastName:this.settingForm.controls.lastName.value,
        address_:this.settingForm.controls.settingAddress.value,
        phoneNumber:this.settingForm.controls.phoneNumber.value
      },
      serviceInfo:{
        haircut:this.settingForm.controls.haircut.value,
        hairdye:this.settingForm.controls.hairdye.value,
        lineUp:this.settingForm.controls.lineUp.value,
        facialTrim:this.settingForm.controls.facialTrim.value
      },
      email:this.settingForm.controls.emailAddress.value,
      businessHours: this.settingForm.controls.businessHours.value
    };
    this.store.dispatch(new updateBusinessAccount(this.profileDetails));
  }
}
