import {Component, OnInit, ViewChild,} from '@angular/core';
import {BarberModel} from '../../models';
import {GooglePlaceDirective} from 'ngx-google-places-autocomplete';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import { ShopService } from '../../service/shop.service';

declare var google: any;
@Component({
  selector: 'app-create-shop',
  templateUrl: './create-shop.component.html',
  styleUrls: ['./create-shop.component.css']
})
export class CreateShopComponent implements OnInit {

  file:File;
  files:Array<File>;
  options:any;
  shopModel:BarberModel;
  shopForm:FormGroup;
  BarberRoles = [
    {'role': 'Owner'},
    {'role': 'Manager'},
  ];

  componentForm = {
    street_number: 'short_name',
    route: 'long_name',
    locality: 'long_name',
    administrative_area_level_1: 'short_name',
    country: 'long_name',
    postal_code: 'short_name'
  };

  componentFormDest = {
    street_number: 'short_name',
    route: 'long_name',
    locality: 'long_name',
    administrative_area_level_1: 'short_name',
    country: 'long_name',
    postal_code: 'short_name'
  };

  @ViewChild("placesRef")
  public gMapElementRef: GooglePlaceDirective;

  constructor(private shopService_:ShopService, private fb:FormBuilder) { }

  ngOnInit() {
    this.options = {
      types:['address'],
      componentRestrictions: { country: 'CA' }
    };
  }




  selectFiles(event){
    if (event.target.files && event.target.files.length > 1) {
      this.files = event.target.files;
    }else{
      this.file = event.target.files[0];
    }
  }

  uploadImage(){
    const formData = new FormData();
    formData.append("photo", this.file);
    this.shopService_.saveImage(formData).subscribe(()=>{
      console.log("it works");
    })
  }

  uploadGallery(){
    const formData = new FormData();
    for(let i = 0; i < this.files.length; i++){
      formData.append("photos", this.files[i],this.files[i].name);
    }
    this.shopService_.saveGallery(formData).subscribe(()=>{
      console.log("it works");
    })
  }

  public handleAddressChange(address: any) {
    console.log(address);

    for (let component in this.componentForm) {
      (<HTMLInputElement>document.getElementById(component)).value = '';
    }

    for(let i = 0; i < address.address_components.length; i++){
      const addressType = address.address_components[i].types[0];
      if(this.componentForm[addressType]){
        (<HTMLInputElement>document.getElementById(addressType)).value = address.address_components[i][this.componentForm[addressType]];
        this.shopForm.controls[addressType].setValue(address.address_components[i][this.componentForm[addressType]]);

      }
    }

  }


}
