import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { ApplicationState } from 'src/app/reducers';
import { returnToUserProfile } from 'src/app/actions';

@Component({
  selector: 'app-payment-received',
  templateUrl: './payment-received.component.html',
  styleUrls: ['./payment-received.component.css']
})
export class PaymentReceivedComponent implements OnInit {

  paymentData:Boolean;
  constructor(private store:Store<any>) { }

  ngOnInit() {
    this.store.select('app').subscribe((state:ApplicationState)=>{
      this.paymentData = false;
    });
  }

  viewProfile(){
    this.store.dispatch(new returnToUserProfile());
  }

}
