import { ADD_BUSINESS_PERSONAL_INFO, ADD_BUSINESS_CONTACT_INFO, CREATE_BUSINESS_ACCOUNT, createBusinessAccount, createBusinessAccountSuccess, createBusinessAccountFailed, ADD_BUSINESS_SERVICES, CREATE_BUSINESS_ACCOUNT_SUCCESS, addBusinesHoursInfo, UPDATE_BUSINESS_ACCOUNT, EDIT_PROFILE_INFO, updateBusinessAccount, updateBusinessAccountSuccess, updateBusinessAccountFailed } from '../actions/business.actions';
import { Injectable } from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {catchError, map, mergeMap, switchMap, tap} from 'rxjs/operators';
import {ClientService, ShopService} from '../service';
import {of} from 'rxjs/observable/of';
import {Router} from '@angular/router';
import { Store, Action } from '@ngrx/store';
import 'rxjs/add/operator/withLatestFrom';
import { ADD_BUSINESS_HOURS } from '../actions/business.actions';
import { GET_PROFILE, getProfileSuccess, getProfileFailed, getProfile, USER_PROFILE, VIEW_PROFILE_APPOINTMENTS, UPDATE_USER_PROFILE, updateUserProfile, LOGOUT_PROFILE, logoutProfileSuccess, VIEW_PROFILE, DELETE_USER_APPOINTMENT, deleteUserAppointment, GET_PROFILE_SUCCESS } from '../actions/profile.actions';
import { loginProfileFailed } from '../actions';
import { ApplicationState } from '../reducers';





@Injectable()
export class BusinessEffects {

businessData:any;
constructor(private store:Store<any>, private actions$: Actions, private shopService: ShopService,private router: Router ) {}


//Business Routing
@Effect({dispatch:false})
addBusinessPersonalInfo$ = this.actions$.pipe(
  ofType(ADD_BUSINESS_PERSONAL_INFO),
  mergeMap(()=> this.router.navigate(['business-account']))
);

@Effect({dispatch:false})
addBusinessContactInfo$ = this.actions$.pipe(
  ofType(ADD_BUSINESS_CONTACT_INFO),
  mergeMap(()=> this.router.navigate(['business-services'])),
);

@Effect({dispatch:false})
viewProfileAppointments$ = this.actions$.pipe(
  ofType(VIEW_PROFILE_APPOINTMENTS),
  mergeMap(()=> this.router.navigate(['business-appointments'])),
);

@Effect({dispatch:false})
addBusinessServiceInfo$ = this.actions$.pipe(
  ofType(ADD_BUSINESS_SERVICES),
  mergeMap(()=> this.router.navigate(['business-hours'])),
);

@Effect({dispatch:false})
  viewProfileBusiness$ = this.actions$.pipe(
    ofType(VIEW_PROFILE),
    mergeMap(() => this.router.navigate(['profile']))
);

@Effect({dispatch:false})
  editClientBusinessAppointment$ = this.actions$.pipe(
    ofType(UPDATE_USER_PROFILE),
    mergeMap(() => this.router.navigate(['edit-client-appointment']))
);

@Effect({dispatch:false})
createBusinessAccountSuccess$ = this.actions$.pipe(
  ofType(CREATE_BUSINESS_ACCOUNT_SUCCESS),
  mergeMap(()=> this.router.navigate(['confirmation'])),
);

@Effect({dispatch:false})
getProfileSuccess$ = this.actions$.pipe(
  ofType(GET_PROFILE_SUCCESS),
  mergeMap(() => this.router.navigate(['profile']))
);

@Effect({dispatch:false})
ReturnBackToUserProfile$ = this.actions$.pipe(
  ofType(USER_PROFILE),
  mergeMap(()=> this.router.navigate(['user-profile'])),
);

@Effect({dispatch:false})
editProfileInfo$ = this.actions$.pipe(
  ofType(EDIT_PROFILE_INFO),
  mergeMap(() => this.router.navigate(['edit-business']))
);


//Business Side Effects
@Effect({dispatch:false})
addBusinessScheduleInfo$ = this.actions$.pipe(
  ofType(ADD_BUSINESS_HOURS),
  map((action:addBusinesHoursInfo)=> this.store.dispatch(new createBusinessAccount())),
);

@Effect()
createBusinessAccount$= this.actions$.pipe(
  ofType(CREATE_BUSINESS_ACCOUNT),
  mergeMap((action)=> { 
    this.store.select('app').subscribe((state:ApplicationState) => {
      this.businessData = state.business;
    })
    return this.shopService.createStore(this.businessData).pipe(
      map((data:any) => {
        if(data.body.success){
          this.router.navigate(['login']);
          return new createBusinessAccountSuccess(data);
        }else{
          return new createBusinessAccountFailed(data);
        }        
      }
      ),
      catchError((err) => of(new createBusinessAccountFailed(err)))
    )
  })
  );
  
  



@Effect()
getBusinessProfile$ = this.actions$.pipe(
  ofType(GET_PROFILE),
  mergeMap(action => this.shopService.getProfileDetails().pipe(
      map((data) => {
        sessionStorage.setItem('BUSINESS_USER',JSON.stringify(data));
        return new getProfileSuccess(data);
      }),
      catchError((err) => of(new getProfileFailed(err)))
    )
  ),
);

@Effect()
updateBusinessProfile$ = this.actions$.pipe(
  ofType(UPDATE_BUSINESS_ACCOUNT),
  mergeMap((action:updateBusinessAccount) => this.shopService.updateProfile(action.payload).pipe(
      map((data:any) => {
        if(data.success){
          this.store.dispatch(new getProfile());
          return new updateBusinessAccountSuccess(data);
        }   
      }),
      catchError((err) => of(new getProfileFailed(err)))
    )
  ),
);



@Effect()
deleteUserAppointment$ = this.actions$.pipe(
  ofType(DELETE_USER_APPOINTMENT),
  mergeMap((action:deleteUserAppointment) => this.shopService.deleteUserAppointment(action.payload).pipe(
      map((data:any) => {
        if(data.success){
          this.store.dispatch(new getProfile());
          this.router.navigate(['business-appointments']);
          return new updateBusinessAccountSuccess(data);
        }   
      }),
      catchError((err) => of(new updateBusinessAccountFailed(err)))
    )
  ),
);

@Effect()
  logoutBusinessProfile$ = this.actions$.pipe(
  ofType(LOGOUT_PROFILE),
  mergeMap((action) => this.shopService.logOutProfile().pipe(
      map((data:any) => {
        if(data.success){     
          this.router.navigate(['login']);
          return new logoutProfileSuccess();
        }
      }),
      catchError((err) => of(new loginProfileFailed(err)))
    )
  ),
);








}

