import { Component, OnInit, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store';
import { editClientInfo, viewClientAppointments, fetchAllBusinessess, viewMyProfile, logoutUser } from 'src/app/actions';
import { ClientAccountState } from 'src/app/models';
import { ApplicationState } from 'src/app/reducers';
import { Subscription } from 'rxjs';
import { SessionService } from 'src/app/service/session-service';

@Component({
  selector: 'user-header',
  templateUrl: './user-header.component.html',
  styleUrls: ['./user-header.component.css']
})
export class UserHeaderComponent implements OnInit, OnDestroy {
  sub:Subscription;
  showHeader:boolean;
  FullName:string;
  userDetails:ClientAccountState;
  constructor(private store:Store<any>, private session:SessionService) { }

  ngOnInit() {
    this.showHeader = false;
    this.sub = this.store.select('app').subscribe((data:ApplicationState) => {
      if(data.client.loaded){
        this.showHeader = true;
        this.userDetails = data.client;
        this.FullName = this.userDetails.firstName+' '+this.userDetails.lastName;
      }else{
        this.session.retriveUserSession();
      }
    });
  }

  viewMyProfile(){
    this.store.dispatch(new viewMyProfile());
  }

  editUserProfile(){
    this.store.dispatch(new editClientInfo());
  }

  viewUserAppointments(){
    this.store.dispatch(new viewClientAppointments());
  }

  findBarbers(){
    this.store.dispatch(new fetchAllBusinessess());
  }

  logoutUser(){
    this.session.clearSessionData();
    this.showHeader = false;
    this.store.dispatch(new logoutUser());
  }

  ngOnDestroy(){
    this.sub.unsubscribe();
  }
  

}
