import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { ClientAccountState } from 'src/app/models';
import { ApplicationState } from 'src/app/reducers';
import { cancelAppointment, editAppointment } from 'src/app/actions';
import { SessionService } from 'src/app/service/session-service';
@Component({
  selector: 'app-user-appointments',
  templateUrl: './user-appointments.component.html',
  styleUrls: ['./user-appointments.component.css']
})
export class UserAppointmentsComponent implements OnInit {
  clientDetails:ClientAccountState;
  constructor(private store:Store<any>, private session:SessionService) {}

  ngOnInit() {
    this.store.select('app').subscribe((state:ApplicationState)=>{
      if(state.client.loaded && state.client){
        this.clientDetails = state.client.appointment;
      }else{
        this.session.retriveUserSession();
      }
    });
  }

  editAppointment(id){
    this.store.dispatch(new editAppointment(id))
  }

  deleteAppointment(id){
    this.store.dispatch(new cancelAppointment(id))
  }

}
