import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import {  Appointment, ClientBookingState } from '../../models/barber.model';
import { createAppointment } from '../../actions/booking.actions';
import { ApplicationState } from 'src/app/reducers';
import { updateAppointment } from 'src/app/actions';
import { SessionService } from 'src/app/service/session-service';

@Component({
  selector: 'app-edit-client-appointment',
  templateUrl: './edit-client-appointment.component.html',
  styleUrls: ['./edit-client-appointment.component.css']
})
export class EditClientAppointmentComponent implements OnInit {

  editAppointment:FormGroup;
  startDate: Date;
  endDate: Date;
  hairStore:Array<Object>;
  twoWeeks: number;
  bookingDetails:Appointment;
  paymentSelection:any;
  paymentAmount:number;
  paidOnlyBookingFee:boolean;
  bookingFee:number = 1500;
  storeData:ClientBookingState;
  shopName:string;
  appointmentDetails:any;


  constructor(private store:Store<any>,private fb:FormBuilder, private session:SessionService) { }

  ngOnInit() {
    this.store.select('app').subscribe((data:ApplicationState)=>{
      if(data.client && data.client.loaded){
        this.appointmentDetails = data.client.appointment;
      }else{
        this.session.retriveUserSession();
      }
    });
    this.twoWeeks = 14;
    this.endDate = this.setValidDate();
    this.hairStore = [
      {'hairjob': 'Hair Cut', 'price':'3000'},
      {'hairjob': 'Line Up', 'price':'2500'},
      {'hairjob': 'Beard Clean Up', 'price':'2500'}
    ]

    this.editUserAppointment();
  }

  editUserAppointment(){
    this.editAppointment = this.fb.group({
      firstName: new FormControl(this.appointmentDetails.firstName, Validators.required),
      lastName: new FormControl(this.appointmentDetails.lastName, Validators.required),
      email: new FormControl(this.appointmentDetails.email, [Validators.required, Validators.email]),
      appointmentDate: new FormControl('', Validators.required),
      job: new FormControl(this.appointmentDetails.serviceType, Validators.required),
    })
  }

  setValidDate(){
    //const twoWeeks:number = 14;
    let currentDay = new Date().getUTCDate();
    let month = new Date().getMonth();
    let year = new Date().getFullYear();
    let hour = new Date().getHours();
    let minutes = new Date().getMinutes();
    let twoWeekFromNow = currentDay + this.twoWeeks;
    this.startDate = new Date(year,month,currentDay,12,30);
    const setEndDate = new Date(year,month,twoWeekFromNow,23,30) ;
    return setEndDate;
  }

  filterSundays = (d: Date): boolean => {
    const day = d.getDay();
    return day !== 6;
  };

  UpdateMyAppointment(){
    this.bookingDetails = {
      shop:this.appointmentDetails.shop,
      id:this.appointmentDetails._id,
      firstName:this.editAppointment.controls.firstName.value,
      lastName:this.editAppointment.controls.lastName.value,
      email:this.editAppointment.controls.email.value,
      appointmentDay:this.editAppointment.controls.appointmentDate.value,
      serviceType: this.editAppointment.controls.job.value.hairjob,
      priceOfService:this.editAppointment.controls.job.value.price,
      balance:this.appointmentDetails.balance,
      fullServicePaid:this.appointmentDetails.fullServicePaid
    }

    this.store.dispatch(new updateAppointment(this.bookingDetails))
  }

}
